import { EventEmitter } from '@angular/core';
var PutDataService = /** @class */ (function () {
    function PutDataService() {
        this.dataReport = new EventEmitter;
        this.dataNews = new EventEmitter;
        this.dataSite = new EventEmitter;
        this.dataMeeting = new EventEmitter;
        this.dataEmployee = new EventEmitter;
        this.dataEmployer = new EventEmitter;
    }
    PutDataService.prototype.updateData = function (data, type) {
        console.log('data: ', data, type);
        switch (type) {
            case '':
                this.dataReport.emit(data);
                break;
            case ' ':
                this.dataNews.emit(data);
                break;
            case '':
            case '':
            case '/':
            case '':
                this.dataSite.emit(data);
                break;
            case ' ':
                this.dataMeeting.emit(data);
                break;
            case '':
                this.dataEmployee.emit(data);
                break;
            case '':
                this.dataEmployer.emit(data);
                break;
        }
    };
    return PutDataService;
}());
export { PutDataService };



